<template>
    <div class="container">
        <TopButton title="장바구니" />
        <div class="check_box">
            <div class="check">
                <input type="checkbox" value="" id="check" checked />
                <label for="check"> 전체선택 </label>
            </div>
            <div class="checkdelete">
                <button><span>선택상품삭제</span></button>
                <button><span>전체상품삭제</span></button>
            </div>
        </div>
        <div class="contents">
            <div class="contentBox">
                <div class="itemName">
                    <input type="checkbox" value="" id="name" checked />
                    <label for="name"> 스킨소스 아데노신 10000앰플 30ml </label>
                </div>
                <div class="itemBox">
                    <div class="itemImg">
                        <img src="" alt="" />
                    </div>
                    <div class="itemOptions">
                        <p>{{ price }} 원</p>
                        <p>1일이내 발송예정</p>
                        <p>옵션</p>
                        <div class="numBox">
                            <p>수량변경</p>
                            <button @click="decrease">-</button>
                            <span>{{ num }}</span>
                            <button @click="increase">+</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="buyBox">
            <div class="buyContent">
                <p>총 {{ num }} 개</p>
                <p>{{ price * num }} <span>원</span></p>
            </div>
            <div class="buyButton">
                <button>구매하기</button>
            </div>
        </div>
    </div>
</template>
<script>
import TopButton from '@/components/TopButton.vue';

export default {
    name: 'ShoppingCart',
    components: {
        TopButton
    },
    data() {
        return {
            num: 1,
            price: 22000
        };
    },
    methods: {
        decrease() {
            if (this.num > 1) {
                this.num -= 1;
            }
        },
        increase() {
            this.num += 1;
        }
    }
};
</script>
<style scoped>
/* 상단 체크박스 */
.check_box {
    margin: 0 auto;
    width: 90%;
    display: flex;
    justify-content: space-between;
}

/* 상품  */
.contents {
    margin: 0 auto;
    width: 90%;
    height: 100vh;
    margin-top: 30px;
    border: 1px solid #999;
    border-radius: 45px 45px 0 0;
}
.contents .contentBox {
    margin: 0 auto;
    width: 85%;
    margin-top: 30px;
    border-bottom: 1px solid #999;
}
.contents .contentBox .itemBox {
    width: 100%;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.contents .contentBox .itemBox .itemImg {
    width: 137px;
    height: 137px;
    background-color: #999;
}
.itemOptions {
    text-align: left;
}
.itemOptions p {
    margin: 0;
}
.itemOptions .numBox {
    display: flex;
}
/* 하단 구매하기 */
.buyBox {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #999;
    height: 210px;
    border-radius: 45px 45px 0 0;
}
.buyBox .buyContent {
    display: flex;
    width: 80%;
    margin: 0 auto;
    justify-content: space-between;
    margin-top: 30px;
}
</style>
