<template>
    <div>
        <div class="header">
            <div class="topBox">
                <div class="col1">
                    <button @click="goBack">
                        <img src="../assets/img/chevron-left-solid.svg" alt="뒤로가기" width="20" height="20" />
                    </button>
                </div>
                <div class="col2">{{ title }}</div>
                <div class="col3">
                    <button @click="link1">
                        <img src="../assets/img/magnifying-glass-solid .svg" alt="검색" width="20" height="20" />
                    </button>
                    <button @click="link2">
                        <img src="../assets/img/bell-regular.svg" alt="알림" width="20" height="20" />
                    </button>
                    <button @click="ShoppingCart">
                        <img src="../assets/img/cart-shopping-solid.svg" alt="장바구니" width="20" height="20" />
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'TopButton',
    props: {
        title: String
    },
    methods: {
        goBack() {
            this.$router.go(-1); // 뒤로가기
        },
        link1() {
            window.location.href = '/';
        },
        link2() {
            window.location.href = '/';
        },
        ShoppingCart() {
            window.location.href = '/shoppingcart';
        }
    }
};
</script>
<style scoped>
.topBox {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    gap: 20px;
    text-align: center;
}
.col1 {
    flex: 1;
}
.col2 {
    font-size: 1.2em;
    font-weight: 600;
    color: black;
    flex: 2;
}
.col3 {
    flex: 1;
}
.col3 > img {
    display: inline-block;
    margin-left: 5px;
}
</style>
